<!--
 * @Description: 视频预览,实时数据,异常报警
 * @Version: 2.0
 * @Autor: Gordon
 * @Date: 2020-03-09 09:13:54
 * @LastEditors: Gordon
 * @LastEditTime: 2020-03-23 15:51:07
 -->

<template style="height: 100%">
  <div class="div-vp g_label2">
    <el-container direction="horizontal">
      <el-aside class="el-aside-vp" width="180px" style="padding: 5px;">
        <el-alert
          ref="alert"
          class="el-alert-vp"
          :closable="false"
          effect="dark"
          title="请选定设备查看"
          center
          show-icon
          type="info"
        ></el-alert>
        <div class="div-vp">
          <gdtree
            lazy
            node-key="id"
            :default-expanded-keys="[0]"
            ref="tree"
            check-strictly
            check-on-click-node
            show-checkbox
            @check-change="personlist"
            :data="treecomdata"
            :props="props"
            :load="loadNode"
            class="el-aside-vp"
            style=" width: 100%; margin-top: 3px"
          ></gdtree>
        </div>
      </el-aside>
      <el-container
        class="el-container-vpM"
        direction="vertical"
        style="padding-left:8px; padding-right:8px"
      >
        <el-header class="el-0-bas" height="58px">
          <div class="div-num div-num-l" align="middle">
            <div class="div-vp div-vp-d" style="padding-bottom: 3px">识别总数</div>
            <div v-if="v_total > 0" class="div-vp div-vp-n">{{v_total}}</div>
            <div v-else class="div-vp div-vp-n">-</div>
          </div>
          <div class="div-num div-num-l" align="middle">
            <div class="div-vp div-vp-d" style="padding-bottom: 3px">体温异常</div>
            <div v-if="v_total > 0" class="div-vp div-vp-n">{{v_unusual}}</div>
            <div v-else class="div-vp div-vp-n">-</div>
          </div>
          <div class="div-num div-num-l" align="middle">
            <div class="div-vp div-vp-d" style="padding-bottom: 3px">未戴口罩</div>
            <div v-if="v_total > 0" class="div-vp div-vp-n">{{v_noMask}}</div>
            <div v-else class="div-vp div-vp-n">-</div>
          </div>
          <div class="div-num div-num-r" align="middle">
            <div class="div-vp div-vp-d" style="padding-bottom: 3px">陌生人</div>
            <div v-if="v_total > 0" class="div-vp div-vp-n">{{v_stranger}}</div>
            <div v-else class="div-vp div-vp-n">-</div>
          </div>
        </el-header>
        <el-main class="el-main-vpB div-middle no-scroll" align="middle">
          <div class="div-vp" style="width: 100%; height: 100%">
            <el-row justify="space-between" type="flex" :gutter="0" class="el-0-bas el-row-photo">
              <el-col :span="24" class="el-0-bas" style="width: 100%;; height: 100%">
                <div
                  ref="ref_realsrc"
                  class="div-vp div-photo div-photo-left"
                  :style="{backgroundImage:'url(' + realsrc + ')'}"
                  align="right"
                >
                  <!-- <img :src="realsrc" style="width: auto; height: auto; max-width: 100%; max-height: 100%;" /> -->
                  <!-- <el-image :src="realsrc" fit="scale-down" class="img-photo">
                    <div slot="placeholder" class="image-slot">
                      加载中
                      <span class="dot">...</span>
                    </div>
                    <div slot="error" class="image-slot">
                      <i class="el-icon-picture-outline"></i>
                    </div>
                  </el-image>-->
                </div>
                <div
                  class="div-vp div-photo div-photo-right"
                  :style="{backgroundImage:'url(' + realsrc_face + ')'}"
                  align="left"
                ></div>
              </el-col>
            </el-row>
            <div
              :class="gettemperatureBorClass(passMessage)"
              style="width:750px; margin:0 auto;"
              align="middle"
            >
              <div class="div-vp div-vp-u now-text">
                <div class="div_inline">
                  <div
                    class="div_inline"
                    v-if="temperature && temperature > 0"
                    :class="gettemperatureClass(passMessage)"
                  >{{temperature}} ℃</div>
                  <img
                    v-if="mounthocc && mounthocc.length > 0"
                    class="temperature_ico"
                    :src="getmaskImg(mounthocc)"
                  />
                </div>
                <div v-if="name && name.length > 0" class="div_inline txt-name">{{name}}</div>
                <div v-if="sex && sex.length > 0" class="div_inline">{{sex}}</div>
                <div v-if="pass && pass.length > 0" class="div_inline">{{pass}}</div>
                <div
                  v-if="device_address && device_address.length > 0"
                  class="div_inline"
                >设备: {{device_address}}</div>
                <div
                  v-if="create_time && create_time > 0"
                  class="div_inline"
                >{{getdatetime(create_time)}}</div>
              </div>
            </div>
          </div>
        </el-main>
        <el-footer class="div-vp el-footer-vp" align="middle">
          <div class="div-vp el-footer-vp" ref="footerwrap">
            <div
              v-for="(o, index) in listdata.slice(0,histNum)"
              :key="index"
              width="12.5%"
              height="100%"
              class="div_inline-block"
              style="margin: -3px 0 0 0; padding: 0px"
            >
              <el-card :body-style="{ padding: '0px' }" shadow="never" class="car-hist">
                <el-card
                  :body-style="{ padding: '0px' }"
                  shadow="never"
                  :class="gettemperatureBorClass(listdata[index].passMessage, true)"
                  style="border-radius: 0px; margin: 0;"
                >
                  <el-image
                    style="width: 90px; height: 100px"
                    :src="getimgurl(listdata[index].base64_picture)"
                    fit="cover"
                  ></el-image>
                  <div style="padding: -1px 0 3px 0; height: 22px">
                    <span
                      :class="gettemperatureClass(listdata[index].passMessage, true)"
                    >{{listdata[index].temperature}} ℃</span>
                    <img
                      class="temperature_ico_s"
                      :src="getmaskImg(listdata[index].mounthocc, true)"
                    />
                  </div>
                </el-card>
                <div
                  class="div-vp g_label2"
                  style="font-size: 12px; padding: 5px 0 4px 0;"
                  align="middle"
                >{{getshorttime(listdata[index].create_time)}}</div>
              </el-card>
            </div>
          </div>
        </el-footer>
      </el-container>
      <el-aside class="el-aside-vp no-scroll" width="235px" align="middle" height="100%">
        <div
          class="div-vp"
          height="20px"
          style="padding: 5px 0 2px 8px; color: #ff5b5b"
          align="left"
        >
          <i class="el-icon-message-solid"></i> 报警记录
        </div>
        <div class="div-vp el-aside-right no-scroll" style="padding-top: 6px" ref="rightwrap">
          <div
            v-for="(o, index) in exclistdata.slice(0,excNum)"
            :key="index"
            width="215px"
            height="80px"
            style="margin: 0px 12px 9px 8px; padding: 0"
          >
            <el-card
              :body-style="{ padding: '0px' }"
              shadow="never"
              :class="gettemperatureBorClass(exclistdata[index].passMessage, true)"
              style="border-radius: 5px; margin: 0; margin:0;"
            >
              <el-image
                style="width: 64px; height: 80px; margin:0px 1px -3px 0px;"
                :src="getimgurl(exclistdata[index].base64_picture)"
                fit="cover"
              ></el-image>
              <el-image
                style="width: 64px; height: 80px; margin:0px 0px -3px 0px;"
                :src="getfaceurl(exclistdata[index].master_map, exclistdata[index].sex)"
                fit="cover"
              ></el-image>
              <div
                class="div-vp div_inline-block"
                style="width:77px; height: 80px; margin:0px 0px -3px 0px;"
                align="left"
              >
                <el-row :gutter="0" style="margin-bottom: 3px">
                  <el-col :span="20">
                    <span
                      :class="gettemperatureClass(exclistdata[index].passMessage, true)"
                      style="font-size: 12px; margin:4px 3px 3px 5px;"
                    >{{exclistdata[index].temperature}} ℃</span>
                  </el-col>
                  <el-col :span="4">
                    <img
                      class="temperature_ico_s"
                      :src="getmaskImg(exclistdata[index].mounthocc, true)"
                      style="width: 14px; float: left; margin-top:5px;"
                    />
                  </el-col>
                </el-row>
                <el-row :gutter="0">
                  <el-col :span="24">
                    <span
                      class="g_label3"
                      style="font-size: 12px; padding: 5px 0 4px 6px;"
                    >{{exclistdata[index].name}}</span>
                  </el-col>
                </el-row>
                <el-row :gutter="0">
                  <el-col :span="24">
                    <span
                      class="g_label3"
                      style="font-size: 12px; padding: 5px 0 4px 6px;"
                    >{{getSex(exclistdata[index].sex)}}</span>
                  </el-col>
                </el-row>
                <el-row :gutter="0">
                  <el-col :span="24">
                    <div
                      class="div-vp g_label3 small-font"
                      style="font-size: 12px; padding: 5px 0 4px 6px; width:86px;"
                    >{{getshorttime(exclistdata[index].create_time)}}</div>
                  </el-col>
                </el-row>
              </div>
            </el-card>
          </div>
        </div>
      </el-aside>
    </el-container>
  </div>
</template>
<script>
import gdtree from '.././tree/src/tree.vue'

export default {
  data() {
    return {
      websock: null,
      centerDialogVisible: true,
      // 按时间筛选数据
      seekTime: ['', ''],
      // 识别记录数据
      listdata: [],
      // 异常记录数据
      exclistdata: [],
      // 异常记录总数
      excTotal: 0,
      // 企业列表数据
      comlistdata: [],
      // 企业树形列表数据
      treecomdata: [],
      // 企业总数
      comTotal: 0,
      // 设备列表数据：
      eqListdata: [],
      // 设备树形列表数据
      treeEqdata: [],
      // 设备总数：
      eqTotal: 0,
      // 正展开的企业(用来拉取设备列表)
      nowCompanyOpen: '',
      // 当前企业:
      nowCompany: '',
      // 当前设备[序列号,可以多个]
      nowEquipment: '',
      // 公司与设备id计数：
      nowTreeID: 0,
      // 第1家企业第1个设备id：
      nowOneComOneEq: -1,
      currentPage: 1,
      pageSize: 99,
      pageTotal: 0,
      // 实时数据：>>>>>>
      info_id: '0',
      name: '', // 未识别人脸
      sex: '', // 男性
      pass: '', // 未识别
      temperature: '', // 36.8
      mounthocc: '', // 戴口罩
      passResult: '', // 通行成功
      passMessage: '', // 体温正常
      serialNo: '', // 5L96X010213
      device_address: '', // 大门
      create_time: 0, // 1584247976336
      base64_picture: '', // 帧图
      master_map: '', // 对库人脸

      // NowFaceStyle: {},
      imgW: 0,
      imgH: 0,

      realsrc_width: 0,
      realsrc_height: 0,
      // 实时数据<<<<<<
      props: {
        label: 'label',
        children: 'zones',
        isLeaf: 'leaf'
      },
      histNum: 9,
      excNum: 9,
      // 数据统计：
      v_total: 0, // 识别总人数
      v_unusual: 0, // 异常人数
      v_noMask: 0, // 未待口罩
      v_stranger: 0 // 陌生人
    }
  },
  components: {
    gdtree: gdtree
  },
  computed: {
    // 获取面板机实时帧图：
    realsrc: function() {
      // this.NowFaceStyle = this.getNowFaceStyle()
      if (this.base64_picture && this.base64_picture.length > 0) {
        return this.getimgurl(this.base64_picture)
      }
      var _sex = this.sex
      var sexN = 'boy'
      if (_sex === '女') {
        sexN = 'girl'
      }
      var imgURL = require('../../assets/img/face_' + sexN + '.png')
      // var _this = this
      // let img = new Image()
      // img.src = imgURL
      // img.onload = function() {
      //   _this.imgW = img.width
      //   _this.imgH = img.height
      //   _this.NowFaceStyle = _this.getNowFaceStyle()
      // }
      return imgURL
    },
    // 获取对库人脸：
    realsrc_face: function() {
      console.log('this.master_map:', this.master_map)
      if (this.master_map && this.master_map.length > 0) {
        return this.getimgurl(this.master_map)
      }
      var _sex = this.sex
      var sexN = 'boy'
      if (_sex === '女') {
        sexN = 'girl'
      }
      return require('../../assets/img/face_' + sexN + '.png')
    },
    // 实时数据：
    videoNowData: function() {
      return {
        info_id: this.info_id,
        name: this.name, // 未识别人脸
        sex: this.sex, // 男性
        pass: this.pass, // 未识别
        temperature: this.temperature, // 36.8
        mounthocc: this.mounthocc, // 戴口罩
        passResult: this.passResult, // 通行成功
        passMessage: this.passMessage, // 体温正常
        serialNo: this.serialNo, // 5L96X010213
        device_address: this.device_address, // 大门
        create_time: this.create_time, // 1584247976336
        base64_picture: this.base64_picture, // 帧图
        master_map: this.master_map // 对库人脸
      }
    }
  },
  created() {
    this.getComList()
  },
  destroyed() {
    if (this.websock) {
      this.websock.close() // 离开路由之后断开websocket连接
    }
  },
  mounted() {
    this.histNum = Math.floor(
      parseInt(window.getComputedStyle(this.$refs.footerwrap).width) / 110
    )
    this.excNum = Math.floor(
      parseInt(window.getComputedStyle(this.$refs.rightwrap).height) / 90
    )
    // var myThis = this
    // 注：window.onresize只能在项目内触发1次
    this.resizefun = () => {
      this.histNum = Math.floor(
        parseInt(window.getComputedStyle(this.$refs.footerwrap).width) / 110
      )
      this.excNum = Math.floor(
        parseInt(window.getComputedStyle(this.$refs.rightwrap).height) / 90
      )
      // this.NowFaceStyle = this.getNowFaceStyle()
    }
    window.addEventListener('resize', this.resizefun)
    this.openNotifyTopLeft('选定1个或多个设备，以实时查看识别画面与数据。')
    // this.NowFaceStyle = this.getNowFaceStyle()
    // var _this = this
    // this.$nextTick(() => {
    //   _this.NowFaceStyle = _this.getNowFaceStyle()
    // })
  },
  // 移除事件监听，避免内存泄漏
  beforeDestroy() {
    window.removeEventListener('resize', this.resizefun)
    this.resizefun = null
  },
  methods: {
    initWebSocket() {
      // 初始化weosocket
      const wsuri = 'ws://39.97.104.66:8085/app/'
      this.websock = new WebSocket(wsuri + this.nowCompany)
      this.websock.onmessage = this.websocketonmessage
      this.websock.onopen = this.websocketonopen
      this.websock.onerror = this.websocketonerror
      this.websock.onclose = this.websocketclose
    },
    websocketonopen() {
      // 连接建立之后执行send方法发送数据
      console.log('WebSocket 连接成功: ')
      // let actions = {"test":"12345"};
      // this.websocketsend(JSON.stringify(actions));
    },
    websocketonerror() {
      // 连接建立失败重连
      this.initWebSocket()
    },
    websocketonmessage(e) {
      // 数据接收
      const data = JSON.parse(e.data)
      console.log('WebSocket 收到消息: ' + data)
      this.checkuddata(data)
      if (data) {
        console.log('this.name:', this.name, data.name)
        console.log('WebSocket: =============================================')
        for (var I in data) {
          console.log('WebSocket:[' + I + ']:' + data[I])
        }
        console.log('WebSocket: =============================================')
        if (data && data.mounthocc && data.mounthocc.length > 0) {
          this.getStats()
          this.getList()
          this.getList(true)
        }
      }
    },
    websocketsend(Data) {
      // 数据发送
      this.websock.send(Data)
    },
    websocketclose(e) {
      // 关闭
      console.log('已断开连接', e)
    },
    // 检测与更新实时数据：
    checkuddata(data) {
      console.log('checkuddata data:', data)
      if (!data) {
        this.info_id = '0'
        this.name = ''
        this.sex = ''
        this.pass = ''
        this.temperature = ''
        this.mounthocc = ''
        this.passResult = ''
        this.passMessage = ''
        this.serialNo = ''
        this.device_address = ''
        this.create_time = 0
        this.base64_picture = ''
        this.master_map = ''
        this.$Storage.localRemove('videoNowData')
        return
      }
      if (data && data.name) {
        if (
          this.nowEquipment &&
          this.nowEquipment.indexOf(data.serialNo) >= 0
        ) {
          this.info_id = data.info_id
          this.name = data.name
          this.sex = data.sex
          this.pass = data.pass
          this.temperature = data.temperature
          this.mounthocc = data.mounthocc
          this.passResult = data.passResult
          this.passMessage = data.passMessage
          this.serialNo = data.serialNo
          this.device_address = data.device_address
          this.create_time = data.create_time
          this.base64_picture = this.getimgurl(data.base64_picture)
          this.master_map = this.getimgurl(data.master_map)
          // this.udData(true)
          console.log('checkuddata:', this.temperature, this.base64_picture)
          this.$Storage.localSet('videoNowData', this.videoNowData)
        }
      } else {
        var SGdata = this.$Storage.localGet('videoNowData')
        console.log('checkuddata SGdata:', SGdata)
        if (SGdata && SGdata.name) {
          this.checkuddata(SGdata)
        }
      }
      console.log('checkuddata this.videoNowData:', this.videoNowData)
    },
    // 获得最新的识别记录：
    getList(_exception) {
      var timeStart = new Date()
      var unusual = false
      if (_exception) {
        unusual = true
      }
      timeStart.setTime(timeStart.getTime() - 3600 * 1000 * 24 * 30)
      var timeEnd = new Date()
      this.seekTime = [
        timeStart.format('yyyy-MM-dd hh:mm:ss'),
        timeEnd.format('yyyy-MM-dd hh:mm:ss')
      ]
      var apiUrl =
        '/api' +
        '/device/find/info' +
        '?page=' +
        this.currentPage +
        '&pageSize=' +
        this.pageSize +
        '&startTime=' +
        this.seekTime[0] +
        '&endTime=' +
        this.seekTime[1] +
        '&unusual=' +
        unusual
      if (this.nowEquipment) {
        apiUrl += '&serialNo=' + this.nowEquipment
      }
      if (this.seekName) {
        apiUrl += '&username=' + this.seekName
      }
      if (this.nowCompany) {
        apiUrl += '&companyName=' + this.nowCompany
      }
      this.$http
        .get(apiUrl)
        .then(res => {
          if (_exception) {
            this.exclistdata = res.data.data.list
            this.excTotal = res.data.data.total
          } else {
            this.listdata = res.data.data.list
            this.pageTotal = res.data.data.total
          }
        })
        // eslint-disable-next-line handle-callback-err
        .catch(err => {
          console.log('获取识别记录失败')
        })
    },
    // 获取公司列表：
    getComList() {
      var apiUrl =
        '/api' + '/company/find/list' + '?page=' + 1 + '&pageSize=' + 10
      this.$http
        .get(apiUrl)
        .then(res => {
          this.comlistdata = res.data.data.list
          this.comTotal = res.data.data.total
          if (this.comTotal > 0 && this.comlistdata) {
            this.nowTreeID = 0
            this.treecomdata = []
            for (var i = 0; i < this.comlistdata.length; i++) {
              // eslint-disable-next-line standard/object-curly-even-spacing
              this.treecomdata.push({
                id: i,
                label: this.comlistdata[i].company_name,
                type: 0
              })
              this.nowTreeID = i
            }
          }
        })
        // eslint-disable-next-line handle-callback-err
        .catch(err => {
          console.log('获取公司列表数据失败')
        })
    },
    // 拉取设备列表：
    getEqList(resolve) {
      var apiUrl =
        '/api' +
        '/device/find/list' +
        '?page=' +
        this.currentPage +
        '&pageSize=4'
      if (this.seekIP) {
        apiUrl += '&deviceIp=' + this.seekIP
      }
      if (this.nowEquipment) {
        apiUrl += '&serialNo=' + this.nowEquipment
      }
      if (this.nowCompanyOpen) {
        apiUrl += '&companyName=' + this.nowCompanyOpen
      }
      this.$http
        .get(apiUrl)
        .then(res => {
          this.eqListdata = res.data.data.list
          this.eqTotal = res.data.data.total
          if (this.eqTotal > 0 && this.eqListdata) {
            this.treeEqdata = []
            // var eqNowID = 0
            for (var i = 0; i < this.eqListdata.length; i++) {
              this.treeEqdata.push({
                id: 'eq' + this.eqListdata[i].device_id,
                label: this.eqListdata[i].device_address, // device_address device_name
                eqdata: this.eqListdata[i],
                type: 1,
                leaf: true
              })
            }
            if (this.nowOneComOneEq === -1) {
              this.nowOneComOneEq = this.nowTreeID + 1
            }
          }
          resolve(this.treeEqdata)
        })
        // eslint-disable-next-line handle-callback-err
        .catch(err => {
          console.log('获取设备列表失败')
        })
    },
    // 拉取统计数据：
    getStats() {
      var apiUrl =
        '/api' +
        '/chart/stats/record?' +
        '&startTime=' +
        this.seekTime[0] +
        '&endTime=' +
        this.seekTime[1]
      if (this.nowEquipment) {
        apiUrl += '&serialNo=' + this.nowEquipment
      }
      if (this.nowCompany) {
        apiUrl += '&companyName=' + this.nowCompany
      }
      this.$http
        .get(apiUrl)
        .then(res => {
          const data = res.data.data
          for (var i in data) {
            this['v_' + data[i].describe] = data[i].sum
          }
        })
        // eslint-disable-next-line handle-callback-err
        .catch(err => {
          console.log('获取设备列表失败')
        })
    },
    // 获取体温文字样式：
    gettemperatureClass: function(_passMessage, _s) {
      if (!_passMessage || _passMessage.length === 0) {
        _passMessage = this.passMessage
      }
      var restr = 'temperature_bas temperature_txt '
      if (_s) {
        restr = 'temperature_bas_s temperature_txt '
      }
      if (_passMessage === '体温正常') {
        return restr + 'temperatureA'
      } else if (_passMessage === '体温过高') {
        return restr + 'temperatureB'
      } else if (_passMessage === '体温过低') {
        return restr + 'temperatureC'
      } else {
        return restr + 'temperatureN'
      }
    },
    // 获取体温边框样式：
    gettemperatureBorClass: function(_passMessage, _s) {
      if (!_passMessage || _passMessage.length === 0) {
        if (!this.passMessage || this.passMessage.length === 0) {
          return 'el-0-bas user-info user-info-no'
        }
        _passMessage = this.passMessage
      }
      if (_passMessage === '体温正常') {
        return 'el-0-bas user-info user-info-green'
      } else if (_passMessage === '体温过高') {
        return 'el-0-bas user-info user-info-red'
      } else if (_passMessage === '体温过低') {
        return 'el-0-bas user-info user-info-blue'
      }
    },
    // 获取是否戴口罩：
    getmaskImg: function(_mounthocc, _s) {
      if (!_mounthocc || _mounthocc.length === 0) {
        _mounthocc = this.mounthocc
      }
      var furl = ''
      if (_s) {
        furl += 's'
      }
      if (_mounthocc === '戴口罩') {
        return require('../../assets/img/mask' + furl + '01.png')
      } else {
        return require('../../assets/img/mask' + furl + '02.png')
      }
    },
    // 获取时间格式
    getdatetime: function(_datetime) {
      return new Date(_datetime).format('yyyy-MM-dd hh:mm:ss')
    },
    // 获取短时间格式
    getshorttime: function(_datetime) {
      return new Date(_datetime).format('MM-dd hh:mm:ss')
    },
    // 获取性别
    getSex(_sex) {
      if (_sex && _sex.length > 0) {
        return _sex
      }
      return '[未注册]'
    },
    // 拉取刷新数据
    udData(_noWebSocket) {
      this.checkuddata(null)
      if (
        this.nowCompany &&
        this.nowCompany.length > 0 &&
        this.nowEquipment &&
        this.nowEquipment.length > 0
      ) {
        this.$refs.alert.visible = false
        this.getStats()
        this.getList()
        this.getList(true)
        if (_noWebSocket) {
        } else {
          // if (this.nowCompany.length > 0 && this.nowEquipment.length > 0) {
          if (this.websock) {
            this.websock.close()
          }
          // }
          this.initWebSocket()
        }
      } else {
        this.$refs.alert.visible = true
        this.listdata = []
        this.exclistdata = []
        this.comlistdata = []
        this.eqListdata = []
        this.v_total = 0
        this.v_unusual = 0
        this.v_noMask = 0
        this.v_stranger = 0
      }
    },
    // 获取与加载企业与设备列表：
    loadNode(node, resolve) {
      if (node.level === 0) {
        return resolve(this.comlistdata)
      }
      if (node.data.label) {
        this.nowCompanyOpen = node.data.label
      }
      if (node.level > 1) return resolve([])
      this.getEqList(resolve)
    },
    personlist(_item, _check) {
      // console.log('gdtree personlist:', _item.label, _item, _check)
      if (_item.label) {
        if (_item.type === 0) {
        } else if (_item.type === 1) {
          this.resNowData(_item, _check)
        }
      }
    },
    // 刷新设备与订阅
    resNowData(_item, _check) {
      if (_check) {
        if (this.nowCompany !== _item.eqdata.company_name) {
          this.nowCompany = _item.eqdata.company_name
          this.nowEquipment = ''
          this.$refs.tree.setCheckedKeys([_item.id])
        }
        if (this.nowEquipment && this.nowEquipment.length > 0) {
          this.nowEquipment += ','
        }
        this.nowEquipment += _item.eqdata.serial
      } else {
        this.nowEquipment = this.nowEquipment.replace(
          new RegExp(_item.eqdata.serial, 'g'), // _item.label
          ''
        )
        this.nowEquipment = this.nowEquipment.replace(
          new RegExp(',,', 'g'),
          ','
        )
        if (this.nowEquipment.charAt(0) === ',') {
          this.nowEquipment = this.nowEquipment.substr(1)
        }
        if (
          this.nowEquipment &&
          this.nowEquipment.charAt(this.nowEquipment.length - 1) === ','
        ) {
          this.nowEquipment = this.nowEquipment.substr(
            0,
            this.nowEquipment.length - 1
          )
        }
      }
      this.udData()
    },
    // 获取照片地址:
    getimgurl(_url) {
      if (!_url || _url.length === 0) {
        return ''
      }
      if (_url.length === 0 || _url.substr(0, 4) === 'http') {
        return _url
      } else {
        return 'data:image/jpg;base64,' + _url
      }
    },
    // 获取对库人脸:
    getfaceurl(_url, _sex) {
      if (!_url || _url.length === 0) {
        var sexN = 'boy'
        if (_sex && _sex === '女') {
          sexN = 'girl'
        }
        return require('../../assets/img/face_' + sexN + '110.png')
      } else {
        return this.getimgurl(_url)
      }
    },
    // 左上角操作指引提示：
    openNotifyTopLeft(_msg) {
      this.$message(_msg)
    },
    // 获得右侧比对人脸的大小与图片样式：
    getNowFaceStyle() {
      var reStyle = {
        backgroundImage: 'url(' + this.realsrc_face + ')'
      }
      if (this.$refs.ref_realsrc && this.imgW > 0) {
        console.log(
          'getNowFaceStyle:',
          this.$refs.ref_realsrc.offsetWidth,
          this.$refs.ref_realsrc.offsetHeight
        )
        // eslint-disable-next-line no-unused-vars
        var imgS = this.imgW / this.imgH
        reStyle.width = this.$refs.ref_realsrc.offsetWidth + 'px'
        reStyle.height = this.$refs.ref_realsrc.offsetHeight + 'px'
      }
      console.log('getNowFaceStyle:', reStyle)
      return reStyle
    }
  }
}
</script>
