<template>
  <div>
    <el-card v-loading="diaLoading">
      <el-row justify="space-between" type="flex" :gutter="20" class="oneRow">
        <el-col :span="24">
          <div class="g_label">
            <div v-if="this.configGd.isAdmin" class="div_inline">
              企业:
              <el-input
                size="small"
                clearable
                class="el-input_co"
                style="width:150px"
                placeholder="请输入企业名称"
                v-model="seekCompanyName"
                @keyup.enter.native="getList"
                @clear="getList"
              />
            </div>
            <div class="g_label div_inline">
              <!-- 筛选按钮 -->
              <el-button
                @click="getList"
                class="el-button_co"
                type="primary"
                icon="el-icon-search"
                :loading="diaLoading"
              >搜索</el-button>
            </div>
            <div class="g_label div_inline div_right">企业总数: {{pageTotal}}</div>
          </div>
        </el-col>
      </el-row>
      <el-row justify="space-between" type="flex" :gutter="20" class="twoRow">
        <el-col :span="24">
          <el-button-group>
            <!-- 添加按钮 -->
            <el-button
              @click="toAddData"
              class="el-button_co"
              type="primary"
              icon="el-icon-circle-plus-outline"
            >添加</el-button>
            <!-- 删除 -->
            <el-button
              @click="doDel"
              class="el-button_co"
              type="danger"
              icon="el-icon-remove-outline"
            >删除</el-button>
            <!-- 分配权限 -->
            <!-- 导入 -->
            <el-button class="el-button_co" type="primary" icon="el-icon-upload2">导入</el-button>
            <!-- 导出 -->
            <el-button class="el-button_co" type="primary" icon="el-icon-download">导出</el-button>
          </el-button-group>
        </el-col>
      </el-row>

      <el-row>
        <!-- 表格 -->
        <el-table
          style="width: 100%"
          :header-cell-style="{background:'#eee',color:'#000'}"
          :data="listdata"
          @selection-change="handleSelectionChange"
        >
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column prop="company_id" label="序号" align="center" width="100px"></el-table-column>
          <el-table-column prop="company_name" label="企业名称" align="center"></el-table-column>
          <el-table-column prop="company_address" label="企业地址" align="center"></el-table-column>
          <el-table-column prop="company_tel" label="企业电话" align="center"></el-table-column>
          <el-table-column label="操作" align="center">
            <template slot-scope="scope">
              <el-button-group>
                <el-button size="mini" icon="el-icon-edit" @click="toEditData(scope)"></el-button>
                <el-button size="mini" icon="el-icon-delete" @click="doDel(scope)"></el-button>
              </el-button-group>
            </template>
          </el-table-column>
        </el-table>
      </el-row>
      <!-- 分页 -->
      <el-row>
        <div class="page_div">
          <el-pagination
            background
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page.sync="currentPage"
            :page-size="pageSize"
            layout="total, prev, pager, next"
            :total="pageTotal"
          ></el-pagination>
        </div>
      </el-row>
    </el-card>

    <!-- 添加对话框： -->
    <el-dialog
      :close-on-click-modal="false"
      :title="adTitle"
      :visible.sync="dialogVisible"
      :destroy-on-close="true"
      width="590px"
    >
      <el-form
        v-loading="diaLoading"
        :validate-on-rule-change="false"
        :model="ruleForm"
        :rules="rules()"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-row style="margin-bottom: -15px">
          <el-col :span="12">
            <el-form-item label="企业ID" prop="company_id" autocomplete="off" v-show="false">
              <el-input v-model="ruleForm.company_id"></el-input>
            </el-form-item>
            <el-form-item label="企业名称" prop="company_name" autocomplete="off">
              <input
                name="us"
                type="text"
                placeholder="企业名称"
                style="position:absolute;left: -9999999px;"
              />
              <el-input
                clearable
                size="small"
                placeholder="请输入企业序列号"
                v-model="ruleForm.company_name"
                :maxlength="20"
                :readonly="readOnlyFlag"
                @focus="readOnlyFlag = false;"
                autocomplete="new-accounts"
                style="width:220px;"
              ></el-input>
            </el-form-item>
            <el-form-item label="企业电话" prop="company_tel" autocomplete="off">
              <el-input
                clearable
                size="small"
                placeholder="请输入企业电话"
                v-model="ruleForm.company_tel"
                :maxlength="20"
                :readonly="readOnlyFlag"
                @focus="readOnlyFlag = false;"
                autocomplete="new-accounts"
                style="width:220px;"
              ></el-input>
            </el-form-item>
            <el-form-item label="企业地址" prop="company_address" autocomplete="off">
              <el-input
                clearable
                size="small"
                placeholder="请输入企业地址"
                v-model="ruleForm.company_address"
                :maxlength="20"
                :readonly="readOnlyFlag"
                @focus="readOnlyFlag = false;"
                autocomplete="new-accounts"
                style="width:420px;"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>

        <el-divider></el-divider>
        <el-form-item style="padding-left: 90px; margin: -10px 0 5px 0">
          <el-button
            ref="submitBut"
            type="primary"
            @click="insert('ruleForm')"
            size="small"
            icon="el-icon-circle-check"
            :loading="diaLoading"
          >{{butTitleSubmit}}</el-button>
          <el-button @click="resetForm('ruleForm')" size="small" icon="el-icon-refresh-left">重置</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      dialogVisible: false,
      diaLoading: false,
      // 添加或修改的企业输入数据：
      ruleForm: {
        company_id: 0,
        company_name: '',
        company_address: '',
        company_tel: ''
      },
      // 要修改的企业信息：
      editCompanyData: {
        company_id: 0,
        company_name: '',
        company_address: '',
        company_tel: ''
      },
      // 企业提交数据检验：
      rulesBas: {
        company_name: [
          { required: true, message: '请输入企业名称', trigger: 'blur' }
        ],
        company_tel: [
          // validator是自定义校验固定写法,我们只需填他的值(checkName)就好了， callback 必须被调用。
          {
            required: true,
            trigger: 'blur',
            validator: this.checkPhone
          }
        ]
      },
      readOnlyFlag: true,
      butTitleSubmit: '立即添加',
      delId: [],
      delNames: [],
      adTitle: '添加企业信息', // 将要操作的内容(标题)
      /** 当前状态：1添加，2修改，3删除 */
      isATyle: 0,

      listdata: [],
      // 筛选企业
      seekCompanyName: '',
      currentPage: 1,
      pageSize: 13,
      pageTotal: 0
    }
  },
  created() {
    this.getList()
  },
  methods: {
    rules() {
      var myReRules = JSON.parse(JSON.stringify(this.rulesBas))
      myReRules.company_tel[0].validator = this.checkPhone
      return myReRules
    },
    // 初始化提交数据：
    intSimData() {
      this.ruleForm = {
        company_id: 0,
        company_name: '',
        company_address: '',
        company_tel: ''
      }
      if (this.isATyle === 2) {
        for (var I in this.editCompanyData) {
          this.ruleForm[I] = this.editCompanyData[I]
        }
      }
    },
    // 提交开始或结束
    udShowStyle(_start, _noInt) {
      this.diaLoading = _start
      if (_start) {
      } else {
        if (_noInt) {
        } else {
          this.intSimData()
        }
      }
    },
    // 重置表单：
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.intSimData()
    },
    // 提交和：
    insert(formName) {
      // var _this = this
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.apiSaveUser()
          this.udShowStyle(true)
        } else {
          return false
        }
      })
    },
    // 提交返回失败的处理：
    urResponseErr(_errmsg) {
      if (this.isATyle !== 3) {
        // this.$refs.upload.abort()
        // this.$refs.upload.clearFiles()
        // this.removeFile()
      }
      this.udShowStyle(false, true)
      this.$message.error(_errmsg)
    },

    // 按钮触发-添加企业信息：
    toAddData() {
      this.isATyle = 1
      this.adTitle = '添加企业信息'
      this.intSimData()
      this.udShowStyle(false)
      this.butTitleSubmit = '立即添加'
      this.dialogVisible = true
    },

    // 按钮触发-编辑修改企业
    toEditData(scope) {
      this.isATyle = 2
      this.adTitle = '修改企业信息'
      this.intSimData()
      for (var I in scope.row) {
        this.editCompanyData[I] = scope.row[I]
      }
      this.udShowStyle(false)
      this.butTitleSubmit = '保存修改'
      this.dialogVisible = true
    },
    // 删除企业按钮
    async doDel(scope) {
      this.isATyle = 3
      if (scope && scope.row) {
      } else if (!this.delId.length) {
        this.$message({ message: '请您选择您需要删除的企业!', type: 'warning' })
        return false
      }
      var myDelIDs = []
      var myNames = ''
      if (scope && scope.row) {
        // this.delId.push(scope.row.company_id)
        // this.delNames.push(scope.row.company_name)
        // this.appDelList(scope.row)
        myNames = scope.row.company_name
        myDelIDs.push(scope.row.company_id)
      } else {
        for (var i = 0; i < this.delNames.length; i++) {
          if (i < 3) {
            myNames += this.delNames[i] + ','
            if (i >= 2 && this.delNames.length > 3) {
              myNames += '...'
            }
          }
          myDelIDs.push(this.delId[i])
        }
        if (myNames.charAt(myNames.length - 1) === ',') {
          myNames = myNames.substr(0, myNames.length - 1)
        }
      }
      var myNumsTip = ''
      if (myDelIDs.length > 1) {
        myNumsTip = ' 等 ' + myDelIDs.length + ' 家企业'
      }
      const confirmResult = await this.$confirm(
        '您确定要删除 [' + myNames + ']' + myNumsTip + ' 吗?',
        '删除企业信息',
        {
          cancelButtonText: '取消',
          confirmButtonText: '确定',
          type: 'warning'
        }
      ).catch(err => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('已取消删除')
      }
      this.udShowStyle(true)
      this.$http
        .post('/api' + '/company/del', myDelIDs)
        .then(res => {
          if (res.data.code === 0) {
            this.dialogVisible = false
            this.$message({
              message: '删除企业信息成功！',
              type: 'success'
            })
            this.udShowStyle(false, true)
            this.getList()
          } else {
            this.urResponseErr('删除企业信息失败：' + res.data.message)
          }
        })
        // eslint-disable-next-line handle-callback-err
        .catch(err => {
          this.urResponseErr('删除企业信息失败：' + err)
        })
    },
    // 提交用户信息
    apiSaveUser() {
      var myBirthday = this.ruleForm.birthday
      var sData = new Date(myBirthday)
      if (!sData || (sData && isNaN(sData.getFullYear()))) {
        myBirthday = ''
      }
      this.udShowStyle(true)
      this.$http
        .post(this.uploadUrl(), this.ruleForm)
        .then(res => {
          if (res) {
            if (res.data.code === 0) {
              this.dialogVisible = false
              this.$message({
                message: this.adTitle + '成功！',
                type: 'success'
              })
              this.getList()
            } else {
              this.urResponseErr(this.adTitle + '失败：' + res.data.message)
            }
          } else {
            this.urResponseErr(this.adTitle + '失败！')
          }
          // this.udShowStyle(false, true)
        })
        // eslint-disable-next-line handle-callback-err
        .catch(err => {
          this.urResponseErr(this.adTitle + '失败:' + err)
          // this.udShowStyle(false, true)
        })
      // eslint-disable-next-line no-unreachable
      this.udShowStyle(false, true)
    },
    // 上传api地址
    uploadUrl: function() {
      if (this.isATyle === 1) {
        return '/api' + '/company/add'
      } else if (this.isATyle === 2) {
        return '/api' + '/company/update'
      }
    },
    // 批量删除前面的复选框
    handleSelectionChange(val) {
      this.delId = []
      this.delNames = []
      val.forEach((ele, indx) => {
        this.delId.push(ele.company_id)
        this.delNames.push(ele.company_name)
      })
    },
    checkPhone(rule, value, callback) {
      if (!value) {
        return callback(new Error('请输入企业电话'))
      }
      // const reg = /^1[3|4|5|7|8][0-9]\d{8}$/
      const phoneReg = /^1[3|4|5|7|8][0-9]{9}$/
      const phoneReg2 = /^(\(\d{3,4}\)|\d{3,4}-|\s)?\d{7,14}$/
      if (phoneReg.test(value) || phoneReg2.test(value)) {
        callback()
      } else {
        return callback(new Error('请输入正确的电话号码'))
      }
    },

    // 获取企业列表数据
    getList() {
      var apiUrl =
        '/api' +
        '/company/find/list' +
        '?page=' +
        this.currentPage +
        '&pageSize=' +
        this.pageSize
      if (this.seekCompanyName) {
        apiUrl += '&company_name=' + this.seekCompanyName
      }
      if (this.seekCompanyAddress) {
        apiUrl += '&company_address=' + this.seekCompanyAddress
      }
      this.udShowStyle(true)
      this.$http
        .get(apiUrl)
        .then(res => {
          if (res.data.code === 0 && res.data.data) {
            this.listdata = res.data.data.list
            this.pageTotal = res.data.data.total
            this.udShowStyle(false)
          } else {
            this.urResponseErr('获取企业列表失败：' + res.data.message)
          }
        })
        // eslint-disable-next-line handle-callback-err
        .catch(err => {
          this.urResponseErr('获取企业列表失败：' + err)
        })
    },
    // 分页
    handleSizeChange(val) {
      // console.log(`每页 ${val} 条`)
    },
    handleCurrentChange(val) {
      // console.log(`当前页: ${val}`)
      this.currentPage = val
      this.getList(this.currentPage)
    }
  }
}
</script>
<style lang="less">

</style>
