<template>
  <div>
    <el-card class="g_label" v-loading="diaLoading">
      <div style="padding: 0 15px 0 15px">
        <el-tabs v-model="activeName">
          <el-tab-pane label="体温阀值配置" name="first" class="g-tab-pane">
            <div class="div_content div_top" style="margin-top: 10px">
              <span class="span_left">最低温:</span>
              <el-input-number
                size="small"
                v-model="lt"
                :precision="1"
                :step="0.1"
                :max="36"
                :min="30"
                :disabled="!configGd.Auth.write"
              ></el-input-number>
            </div>
            <div class="div_content div_button">
              <span class="span_left">最高温:</span>
              <el-input-number
                size="small"
                v-model="gt"
                :precision="1"
                :step="0.1"
                :max="40"
                :min="36"
                :disabled="!configGd.Auth.write"
              ></el-input-number>
            </div>
          </el-tab-pane>
          <el-tab-pane label="警告提醒配置" name="second" class="g-tab-pane">
            <el-row v-for="(oitem,index) in OPsecondList" :key="index" type="flex" :gutter="20" class="oneRow">
              <el-col :span="1" class="card-title-s" style="min-width: 140px;" align="right">{{oitem.label}}</el-col>
              <el-col :span="1" class="card-title-s" style="min-width: 100px;" align="left">
                <el-switch v-model="oitem.check" :active-value="true" :inactive-value="false" :disabled="!configGd.Auth.write"></el-switch>
              </el-col>
              <el-col :span="20" class="card-title-s" style="min-width: 100px;" align="left">
                <el-checkbox v-for="(osub,subindex) in oitem.sets" :key="subindex" v-model="osub.check">{{osub.label}}</el-checkbox>
              </el-col>
            </el-row>
            <el-row type="flex" :gutter="20" class="oneRow" style="margin-top: 30px">
              <el-col
                :span="1"
                class="card-title-s"
                style="min-width: 140px;"
                align="right"
              >接收报警手机号码</el-col>
              <el-col :span="20" align="left">
                <el-tag
                  v-for="(phone,index) in B_phones"
                  :key="index"
                  :closable="configGd.Auth.write"
                  size="small"
                  @close="delPhone(phone,index)"
                  style="margin: -0 3px 0 0; color: #666666;"
                  :disabled="!configGd.Auth.write"
                >{{phone}}</el-tag>
                <el-row type="flex" :gutter="20" class="oneRow" style="margin-top: 0px">
                  <el-col :span="10" align="left" style="min-width: 205px; width: 205px">
                    <el-input
                      placeholder="请输入手机号码"
                      v-model="B_inputPhone"
                      size="small"
                      @input="inputPhone"
                      @change="changePhone"
                      maxlength="11"
                      show-word-limit
                      clearable
                      prefix-icon="el-icon-mobile-phone"
                      style="min-width: 200px;"
                      :disabled="!configGd.Auth.write"
                    ></el-input>
                    <el-alert
                      ref="alert"
                      :closable="false"
                      :title="inputPhoneMsg"
                      type="error"
                      v-show="inputPhoneMsg"
                      style="padding: 3px 3px 0px 0; background: #FFFFFF !important; margin-bottom: -22px"
                    ></el-alert>
                  </el-col>
                  <el-col :span="20" align="left">
                    <el-button
                      @click="addPhoneHar"
                      type="primary"
                      size="small"
                      plain
                      icon="el-icon-circle-plus-outline"
                      :disabled="!configGd.Auth.write"
                    >添加</el-button>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
            <el-row type="flex" :gutter="20" class="oneRow" style="margin-top: 2px">
              <el-col
                :span="1"
                class="card-title-s"
                style="min-width: 140px;"
                align="right"
              >接收报警邮箱地址</el-col>
              <el-col :span="20" align="left">
                <el-tag
                  v-for="(email,index) in B_emails"
                  :key="index"
                  :closable="configGd.Auth.write"
                  size="small"
                  @close="delEmail(email,index)"
                  style="margin: -0 3px 0 0; color: #666666;"
                  :disabled="!configGd.Auth.write"
                >{{email}}</el-tag>
                <el-row type="flex" :gutter="20" class="oneRow" style="margin-top: 0px">
                  <el-col :span="10" align="left" style="min-width: 205px; width: 205px">
                    <el-input
                      placeholder="请输入邮箱地址"
                      v-model="B_inputEmail"
                      size="small"
                      @input="inputEmail"
                      @change="changeEmail"
                      clearable
                      prefix-icon="el-icon-message"
                      style="min-width: 200px;"
                      :disabled="!configGd.Auth.write"
                    ></el-input>
                    <el-alert
                      ref="alert"
                      :closable="false"
                      :title="inputEmailMsg"
                      type="error"
                      v-show="inputEmailMsg"
                      style="padding: 3px 3px 0px 0; background: #FFFFFF !important; margin-bottom: -22px"
                    ></el-alert>
                  </el-col>
                  <el-col :span="20" align="left">
                    <el-button
                      @click="addEmailHar"
                      type="primary"
                      size="small"
                      plain
                      icon="el-icon-circle-plus-outline"
                      :disabled="!configGd.Auth.write"
                    >添加</el-button>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </el-tab-pane>
        </el-tabs>
      </div>
      <el-row justify="space-between" type="flex" :gutter="20" class="twoRow">
        <el-col :span="24">
          <el-divider></el-divider>
          <div class="div_content div-bottom-button div_right">
            <!-- 保存按钮 -->
            <el-button
              @click="setLimit"
              class="el-button_co"
              type="primary"
              icon="el-icon-circle-check"
              :loading="diaLoading"
              :disabled="!configGd.Auth.write"
            >保存</el-button>
          </div>
        </el-col>
      </el-row>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      diaLoading: false,
      activeName: 'first',

      B_phones: ['13988572581', '18666666666', '1987525874'],
      B_emails: ['afeaef@163.com', 'awefaf@qq.com', 'qweras@live.com'],
      B_inputPhone: '',
      B_inputEmail: '',
      inputPhoneMsg: '',
      inputEmailMsg: '',

      OPsecondList: [
        {
          name: 'unusual',
          label: '体温异常',
          check: true,
          sets: [
            { name: 'email', label: '邮件', check: true },
            { name: 'sms', label: '手机短信', check: true }
          ]
        },
        {
          name: 'noMask',
          label: '未戴口罩',
          check: true,
          sets: [
            { name: 'email', label: '邮件', check: true },
            { name: 'sms', label: '手机短信', check: true }
          ]
        },
        {
          name: 'stranger',
          label: '陌生人',
          check: true,
          sets: [
            { name: 'email', label: '邮件', check: true },
            { name: 'sms', label: '手机短信', check: true }
          ]
        }
      ],
      companyName: '蓝普金睛', // 当前配置公司名
      OPsecondAlerm: [
        {
          unusual: true,
          email: false,
          sms: true
        },
        {
          noMask: true,
          email: false,
          sms: true
        },
        {
          stranger: true,
          email: false,
          sms: true
        }
      ],

      gt: 0, // 最高温(double)
      lt: 0 // 最低温(double)
    }
  },
  created() {
    // console.log(new Date().format("yyyy-MM-dd hh:mm:ss"))
    // console.log(this.seekTime)
    this.getLimit()

    this.OPsecondSetData()
  },
  methods: {
    getLimit() {
      this.udShowStyle(true)
      this.$http
        .get('/api' + '/device/find/limit')
        .then(res => {
          if (res.data && res.data.code === 0 && res.data.data) {
            this.configGd.limit = [res.data.data.lt, res.data.data.gt]
            this.lt = res.data.data.lt
            this.gt = res.data.data.gt
            this.udShowStyle(false)
          } else {
            this.urResponseErr('查询体温报警阀值失败：' + res.data.message)
          }
        })
        // eslint-disable-next-line handle-callback-err
        .catch(err => {
          this.urResponseErr('查询体温报警阀值失败：' + err)
        })
    },

    // 体温阀值修改提交：
    setLimit() {
      this.udShowStyle(true)
      this.$http
        .get('/api' + '/device/set/limit' + '?lt=' + this.lt + '&gt=' + this.gt)
        .then(res => {
          console.log('res.data:', res.data)
          if (res.data && res.data.code === 0) {
            this.$message({
              message: '设置体温报警阀值成功',
              type: 'success'
            })
            this.udShowStyle(false)
          } else {
            this.urResponseErr('设置体温报警阀值失败：' + res.data.message)
          }
        })
        // eslint-disable-next-line handle-callback-err
        .catch(err => {
          this.urResponseErr('获取公司列表数据失败：' + err)
        })
    },

    // 警告报酬配置数据赋值
    OPsecondSetData() {
      this.OPsecondAlerm.forEach((ele, index) => {
        // this.OPsecondList[index]
        for (let I in ele) {
          if (I === this.OPsecondList[index].name) {
            this.OPsecondList[index].check = ele[I]
          } else {
            this.OPsecondList[index].sets.forEach((Iele, Iindex) => {
              if (I === Iele.name) {
                Iele.check = ele[I]
              }
            })
          }
        }
      })
      // console.log('OPsecondAlerm OPsecondList:', this.OPsecondList)
    },

    // 添加报警手机号码-按钮点击
    addPhoneHar() {
      if (this.inputPhoneMsg) {
        return
      }
      if (
        this.B_inputPhone &&
        this.B_phones.indexOf(this.B_inputPhone) === -1
      ) {
        this.B_phones.push(this.B_inputPhone)
        this.B_inputPhone = ''
      }
    },
    // 添加报警手机号码-按钮点击
    addEmailHar() {
      if (this.inputEmailMsg) {
        return
      }
      if (
        this.B_inputEmail &&
        this.B_emails.indexOf(this.B_inputEmail) === -1
      ) {
        this.B_emails.push(this.B_inputEmail)
        this.B_inputEmail = ''
      }
    },

    // // 选项卡切换
    // handleClick(tab, event) {
    //   console.log('handleClick:', tab, event)
    // },

    // 移除报警手机号码
    delPhone(phone, index) {
      console.log('delPhone:', phone, index)
      this.B_phones.splice(this.B_phones.indexOf(phone), 1)
    },
    // 移除报警手机号码
    delEmail(email, index) {
      console.log('delEmail:', email, index)
      this.B_emails.splice(this.B_emails.indexOf(email), 1)
    },

    // 手机号码输入检验
    inputPhone(value) {
      console.log('inputPhone value:', value)
      if (value) {
        this.B_inputPhone = '' + parseInt(this.B_inputPhone)
      }
      if (this.B_inputPhone === 'NaN') {
        this.B_inputPhone = ''
      }
      this.inputPhoneMsg = ''
    },
    // 手机号码输入验证
    changePhone(value) {
      if (value) {
        var partten = /^(13[0-9]|14[0-9]|15[0-9]|16[6]|18[0-9]|19[6,9]|17[0-9])\d{8}$/i
        if (partten.test(value)) {
        } else {
          this.inputPhoneMsg = '请输入正确的手机号码'
        }
      } else {
        this.inputPhoneMsg = ''
      }
    },

    // 提交返回失败的处理：
    urResponseErr(_errmsg) {
      this.udShowStyle(false)
      this.$message.error(_errmsg)
    },
    // 提交开始或结束
    udShowStyle(_start) {
      this.diaLoading = _start
    },

    // 手机号码输入检验
    inputEmail(value) {
      console.log('inputEmail value:', value)
    },
    // 手机号码输入验证
    changeEmail(value) {
      if (value) {
        var partten = /^([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/
        if (partten.test(value)) {
        } else {
          this.inputEmailMsg = '请输入有效的电子邮箱'
        }
      } else {
        this.inputEmailMsg = ''
      }
    }
  }
}
</script>
<style lang="less">
.g-tab-pane {
  padding: 10px 0 10px 0;
}
</style>
