<!--
 * @Description: 数据管理-识别记录
 * @Version: 2.0
 * @Autor: Gordon
 * @Date: 2020-03-09 09:13:54
 * @LastEditors: Gordon
 * @LastEditTime: 2020-03-27 15:01:39
 -->

<template>
  <div>
    <el-card class="g_label" v-loading="diaLoading">
      <el-row justify="space-between" type="flex" :gutter="20" class="oneRow">
        <el-col :span="24">
          <div class="div_inline">
            时间:
            <el-date-picker
              class="el-date-picker-suffix"
              v-model="seekTime"
              size="small"
              type="datetimerange"
              range-separator="至"
              start-placeholder="开始时间"
              end-placeholder="结束时间"
              value-format="yyyy-MM-dd HH:mm:ss"
              @change="date_picker_change"
            ></el-date-picker>
          </div>
        </el-col>
      </el-row>
      <el-row justify="space-between" type="flex" :gutter="20" class="oneRow" style="margin-top: -8px;">
        <el-col :span="24">
          <div v-if="this.configGd.isAdmin" class="div_inline">
            企业:
            <el-select
              v-model="companyName"
              clearable
              size="small"
              @change="getList"
              @clear="getList"
              style="width:200px; margin-left: 5px;"
              placeholder="请选择企业"
            >
              <el-option
                v-for="item in comlistdata"
                :key="item.company_name"
                :label="item.company_name"
                :value="item.company_name"
              ></el-option>
            </el-select>
          </div>
          <div class="div_inline" style="margin-left: 10px;">
            <span style="color: #FF0000">异常:</span>
            <el-select
              v-model="alertSeek"
              clearable
              size="small"
              @change="alertChange"
              @clear="alertChange"
              style="width:160px; margin-left: 10px;"
              placeholder="请选择异常"
            >
              <el-option
                v-for="item in alertList"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </div>
          <div class="div_inline">
            用户名:
            <el-input
              clearable
              size="small"
              class="el-input_co"
              style="width:132px"
              placeholder="请输入用户名"
              v-model="seekName"
              @keyup.enter.native="getList"
              @clear="getList"
            />
          </div>
          <div class="div_inline_button" style="margin-left: 7px;">
            <!-- 筛选按钮 -->
            <el-button @click="getList" class="el-button_co" type="primary" icon="el-icon-search" :loading="diaLoading">搜索</el-button>
          </div>
          <div class="div_inline_button" style="margin-left: 15px;">
            <!-- 刷新按钮 -->
            <el-button
              @click="getList(true)"
              class="el-button_co"
              type="success"
              icon="el-icon-refresh"
              :loading="diaLoading"
            >刷新</el-button>
          </div>
          <div class="div_inline div_right">记录总数: {{pageTotal}}</div>
        </el-col>
      </el-row>
      <el-row>
        <!-- 表格 -->
        <el-table
          style="width: 100%"
          :header-cell-style="{background:'#eee',color:'#000'}"
          :data="listdata"
          stripe
        >
          <el-table-column prop="info_id" label="序号" align="center" width="100px"></el-table-column>
          <el-table-column prop="base64_picture" label="帧图" align="center">
            <template slot-scope="scope">
              <div class="baseImage">
                <img :src="getimgurl(scope.row.base64_picture)" />
              </div>
            </template>
          </el-table-column>
          <el-table-column prop="temperature" label="体温" align="center">
            <template slot-scope="scope">
              <el-card shadow="never" :class="gettemperatureClass(scope.row.passMessage)">
                <div class="temperature_div">
                  <span class="temperature_txt">{{scope.row.temperature}}</span>
                  <img class="temperature_ico" :src="getmaskImg(scope.row.mounthocc)" />
                </div>
              </el-card>
            </template>
          </el-table-column>
          <el-table-column prop="name" label="用户名" align="center"></el-table-column>
          <el-table-column prop="passResult" label="是否通行" align="center"></el-table-column>
          <el-table-column v-if="this.configGd.isAdmin" prop="company_name" label="所属企业" align="center"></el-table-column>
          <!-- <el-table-column prop="serialNo" label="设备序列号" align="center"></el-table-column> -->
          <el-table-column prop="device_address" label="位置" align="center"></el-table-column>
          <el-table-column prop="create_time" label="识别时间" align="center"></el-table-column>
          <el-table-column prop label="操作" align="center">
            <template slot-scope="scope">
              <el-button-group>
                <el-button size="mini" icon="el-icon-document" @click="doShowInfo(scope)"></el-button>
              </el-button-group>
            </template>
          </el-table-column>
        </el-table>
      </el-row>
      <!-- 分页 -->
      <el-row>
        <div class="page_div">
          <el-pagination
            background
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page.sync="currentPage"
            :page-size="pageSize"
            layout="total, prev, pager, next"
            :total="pageTotal"
          ></el-pagination>
        </div>
      </el-row>
    </el-card>

    <!-- 用户详细信息对话框： -->
    <el-dialog
      title="识别记录详细信息"
      :visible.sync="InfoDialogVisible"
      :destroy-on-close="true"
      width="760px"
    >
      <el-row justify="space-between" type="flex" :gutter="0" style="margin-top: -20px; margin-bottom: -15px;">
        <el-col :span="6" class="g_label2" style="padding:0; margin: 0;" align="left">
          <el-card
            shadow="never"
            :class="gettemperatureClass(recordInfo.passMessage)"
            style="padding:0; margin: -4px 0 0 15px; width: 72px; height: 24px;"
          >
            <div class="temperature_div" style="margin-left: -3px; margin-top: -7px">
              <span class="temperature_txt">{{recordInfo.temperature}}</span>
              <img class="temperature_ico" :src="getmaskImg(recordInfo.mounthocc)" />
            </div>
          </el-card>
        </el-col>
        <el-col :span="6" class="g_label2" style="padding:0; margin: 0;" align="left">
          <span style="font-size:15px; font-weight: bold; margin-right: 8px;">{{recordInfo.name}}</span>
          <span>({{recordInfo.sex}})</span>
        </el-col>
        <el-col
          :span="6"
          class="g_label2"
          style="padding:0; margin: 0;"
        >{{recordInfo.device_address}}</el-col>
        <el-col :span="5" class="g_label2" style="padding:0; margin: 0;" align="left">
          <span>{{recordInfo.passResult}}</span>
        </el-col>
        <el-col :span="6" class="g_label2" style="padding:0; margin: 0;">{{recordInfo.create_time}}</el-col>
        <el-col :span="1"></el-col>
      </el-row>
      <el-divider></el-divider>
      <el-row justify="space-between" type="flex" :gutter="0" style="margin-top: -25px;">
        <el-col :span="24" class="g_labelBox" style="width: 100%;; height: 380px;">
          <div
            class="div-rc-vp div-rc-photo div-rc-photo-left"
            :style="{backgroundImage:'url(' + getPciSrc(recordInfo.base64_picture) + ')'}"
            align="right"
          >
          </div>
          <div
            class="div-rc-vp div-rc-photo div-rc-photo-right"
            :style="{backgroundImage:'url(' + getPciSrc(recordInfo.master_map) + ')'}"
            align="left"
          ></div>
        </el-col>
      </el-row>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      diaLoading: false,
      // 按时间筛选数据
      seekTime: ['', ''],
      // 异常筛选：
      alertSel: {
        all: false,
        unusual: false,
        noMask: false,
        stranger: false
      },
      comlistdata: [],
      // 企业总数
      comTotal: 0,
      // 按企业筛选
      companyName: '',
      // 按用户名筛选数据
      seekName: '',
      // 按设备序列号筛选数据
      // seekSerialNo: '',
      // 设备表单数据
      listdata: [],
      // 选择设备
      devicename: [],
      currentPage: 1,
      pageSize: 6,
      pageTotal: 0,
      /** 异常筛选 */
      alertSeek: '',
      alertList: [
        { label: '全部异常', value: 'allaler' },
        { label: '体温异常', value: 'unusual' },
        { label: '未戴口罩', value: 'noMask' },
        { label: '陌生人', value: 'stranger' }
      ],
      InfoDialogVisible: false, // 识别记录详细信息对话框
      // 要查看的识别记录信息：
      recordInfo: {
        info_id: 0,
        name: '',
        create_time: '',
        device_address: '',
        sex: '',
        master_map: '',
        base64_picture: '' // 照片地址
      }
    }
  },
  created() {
    // console.log(new Date().format("yyyy-MM-dd hh:mm:ss"))
    // console.log(this.seekTime)
    var timeStart = new Date()
    timeStart.setTime(timeStart.getTime() - 3600 * 1000 * 24 * 30)
    var timeEnd = new Date()
    this.seekTime = [
      timeStart.format('yyyy-MM-dd hh:mm:ss'),
      timeEnd.format('yyyy-MM-dd hh:mm:ss')
    ]
    this.getList()
    this.getComList()
  },
  methods: {
    getList(_res) {
      console.log('this.seekTime:', this.seekTime, !this.seekTime, !this.seekTime[0], !this.seekTime[1])
      if (_res || !this.seekTime || !this.seekTime[0] || !this.seekTime[1]) {
        var timeStart = new Date()
        timeStart.setTime(timeStart.getTime() - 3600 * 1000 * 24 * 30)
        var timeEnd = new Date()
        this.seekTime = [
          timeStart.format('yyyy-MM-dd hh:mm:ss'),
          timeEnd.format('yyyy-MM-dd hh:mm:ss')
        ]
      }
      var apiUrl =
        '/api' +
        '/device/find/info' +
        '?page=' +
        this.currentPage +
        '&pageSize=' +
        this.pageSize +
        '&startTime=' +
        this.seekTime[0] +
        '&endTime=' +
        this.seekTime[1] +
        '&all=' +
        this.alertSel.all +
        '&unusual=' +
        this.alertSel.unusual +
        '&noMask=' +
        this.alertSel.noMask +
        '&stranger=' +
        this.alertSel.stranger
      if (this.seekName) {
        apiUrl += '&username=' + this.seekName
      }
      if (this.companyName) {
        apiUrl += '&companyName=' + this.companyName
      }
      this.udShowStyle(true)
      this.$http
        .get(apiUrl)
        .then(res => {
          if (res.data.code === 0 && res.data.data) {
            this.listdata = res.data.data.list
            this.pageTotal = res.data.data.total
            this.udShowStyle(false)
          } else {
            this.urResponseErr('获取识别记录失败：' + res.data.message)
          }
        })
        // eslint-disable-next-line handle-callback-err
        .catch(err => {
          this.urResponseErr('获取识别记录失败：' + err)
        })
    },
    // 获取公司列表：
    getComList() {
      var apiUrl =
        '/api' + '/company/find/list' + '?page=' + 1 + '&pageSize=' + 10
      this.udShowStyle(true)
      this.$http
        .get(apiUrl)
        .then(res => {
          if (res.data.code === 0 && res.data.data) {
            this.comlistdata = res.data.data.list
            this.comTotal = res.data.data.total
            this.udShowStyle(false)
          } else {
            this.urResponseErr('获取公司列表数据失败：' + res.data.message)
          }
        })
        // eslint-disable-next-line handle-callback-err
        .catch(err => {
          this.urResponseErr('获取公司列表数据失败：' + err)
        })
    },
    // 异常筛选设置：
    alertChange(val) {
      console.log('alertChange val:', val)
      this.alertSel.all = false
      this.alertSel.unusual = false
      this.alertSel.noMask = false
      this.alertSel.stranger = false
      if (val === 'allaler') {
        // this.alertSel.unusual = true
        // this.alertSel.noMask = true
        // this.alertSel.stranger = true
        this.alertSel.all = true
      } else {
        this.alertSel[val] = true
      }
      this.getList()
    },
    // 查询设备按钮http://localhost:8085/device/find/info?startTime=2020-03-04 18:10:21&endTime=2020-03-04 18:15:23&username=黄新乐
    pictrue(scope) {
      console.log(scope.row)
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url
      this.dialogVisible = true
      console.log(file.url)
    },
    // 分页
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`)
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`)
      this.currentPage = val
      this.getList(this.currentPage)
    },
    date_picker_change(val) {
      console.log(`时间: ${val}`)
      console.log(this.seekTime)
      this.getList(this.currentPage)
    },
    // 获取是否戴口罩：
    getmaskImg(_mounthocc) {
      if (_mounthocc === '戴口罩') {
        return require('../../assets/img/mask01.png')
      } else {
        return require('../../assets/img/mask02.png')
      }
    },
    // 获取体温样式：
    gettemperatureClass(_passMessage) {
      if (_passMessage === '体温正常') {
        return 'temperature temperatureA'
      } else if (_passMessage === '体温过高') {
        return 'temperature temperatureB'
      } else if (_passMessage === '体温过低') {
        return 'temperature temperatureC'
      }
    },
    // 获取照片地址:
    getimgurl(_url) {
      if (_url.length === 0 || _url.substr(0, 4) === 'http') {
        return _url
      } else {
        return 'data:image/jpg;base64,' + _url
      }
    },
    // 预览详细信息接口
    doShowInfo(scope) {
      if (scope && scope.row) {
        this.recordInfo = scope.row
      }
      this.InfoDialogVisible = true
    },
    // 获取面板机实时帧图：
    getPciSrc(_imgurl) {
      // this.NowFaceStyle = this.getNowFaceStyle()
      if (_imgurl && _imgurl.length > 0) {
        return this.getimgurl(_imgurl)
      }
      var _sex = this.sex
      var sexN = 'boy'
      if (_sex === '女') {
        sexN = 'girl'
      }
      var imgURL = require('../../assets/img/face_' + sexN + '.png')
      return imgURL
    },

    // 提交返回失败的处理：
    urResponseErr(_errmsg) {
      this.udShowStyle(false)
      this.$message.error(_errmsg)
    },
    // 提交开始或结束
    udShowStyle(_start) {
      this.diaLoading = _start
    }
  }
}
</script>
<style lang="less" scoped>
.temperature {
  width: 80px;
  height: 30px;
  margin: 0 auto;
  border-radius: 15px;
  text-align: left;
  line-height: 0px;
  font-size: 14px;
  font-weight: 600;
  border-width: 2px;
  margin-top: 0px;
  .temperature_div {
    margin-top: -6px;
  }
  .temperature_txt {
    margin-left: -8px;
    margin-top: 0px;
  }
  .temperature_ico {
    margin-left: 30px;
    margin-top: -14px;
  }
}
.temperatureA {
  color: #008e2b;
  border-color: #8cd28f;
}
.temperatureB {
  color: #ffffff;
  border-color: #e60012;
  background-color: #e60012;
}
.temperatureC {
  color: #ffffff;
  border-color: #0080ff;
  background-color: #0080ff;
}

.checkbox-un {
  color: #ff0006 !important;
  border-color: #ff0006 !important;
  //background-color: #ffb9b7 !important;
}

.titleMessage {
  color: black;
  padding-bottom: 0.2rem;
  font-weight: 700;
  text-align: left;
  span {
    font-weight: 500;
    font-size: 0.1rem;
  }
}
.basicMessage {
  color: #3c4e86;
  b {
    display: inline-block;
    width: 1rem;
    text-align: right;
    height: 0.5rem;
    font-size: 0.2rem;
    padding-right: 0.1rem;
  }
  // input {
  //   height: 0.3rem;
  //   width: 3.5rem;
  // }
  em {
    width: 4rem;
    display: inline-block;
  }
  // .el-input {
  //   display: inline;
  // }
}
// 上传照片
.upPicture {
  position: relative;
  .uploadPicture {
    position: absolute;
    left: 1.17rem;
    top: 0.21rem;
    button {
      position: relative;
      width: 1rem;
      height: 0.45rem;

      span {
        position: absolute;
        left: 0.16rem;
        top: 0.13rem;
        font-size: 0.16rem;
      }
    }
  }
}
.baseImage {
  img {
    height: 80px;
  }
}

.div-rc-vp {
  margin: 0;
  padding: 0;
}
.div-rc-photo {
  width: 48%;
  height: 380px;
  display: inline-block;
  background-repeat: no-repeat;
  background-position-x: center;
  background-position-y: center;
  background-size: contain;
}
.div-rc-photo-left {
  margin-right: 4px;
  background-position-x: right;
}
.div-rc-photo-right {
  margin-left: 4px;
  background-position-x: left;
}
// .isTemperature {
//   img {

//   }
// }
</style>
